<template>
  <q-drawer
    :dark="$root.dark"
    show-if-above
    mini-to-overlay
    side="left"
    :mini="miniState"
    :bordered="$route.params?.settings_tab ? true : false"
    v-model="$root.mainDrawer"
    :breakpoint="mainDrawerBreakpoint"
    :mini-width="$root.mainDrawerCfg.mini"
    :width="$root.mainDrawerCfg.width"
    @mouseover="updateMiniState(true)"
    @mouseout="updateMiniState(true)"
    class="drawer-main"
    :class="{
      ['drawer-main-dark']: $root.dark,
      ['shadows-lg']: !$route.params?.settings_tab,
    }"
    behavior="desktop"
  >
    <q-scroll-area
      class="fit"
      :horizontal-thumb-style="{ opacity: 0 }"
      :vertical-thumb-style="{ opacity: 0 }"
    >
      <q-list padding>
        <q-item
          clickable
          to="/"
          class="menu-logo bg-none text-primary rounded padding-6t padding-7b"
        >
          <q-item-section :avatar="miniState">
            <GrowmodoTextIcon
              noTextHover
              :dark="$root.dark"
              size="32px"
              :text="!miniState"
            />
          </q-item-section>
        </q-item>

        <q-item
          clickable
          class="menu-item menu-request cursor-pointer q-mx-sm"
          :class="{
            ['bg-none text-primary']: miniState,
            [$root.dark
              ? 'bg-primary-50 text-primary'
              : 'bg-primary text-white']: !miniState,
          }"
          style="transition: all ease-in-out"
          @click="$root.toggleRequest()"
        >
          <q-item-section avatar style="min-width: unset" class="q-pr-sm">
            <q-avatar
              rounded
              size="44px"
              class="rounded"
              :style="{
                ['margin-left']: !miniState ? '-10px' : 'unset',
              }"
            >
              <q-icon name="icon-plus" size="sm" class="q-pa-xs" />
            </q-avatar>
          </q-item-section>
          <q-item-section v-if="!miniState" class="no-pointer-events">
            <q-item-label>Add a Request</q-item-label>
          </q-item-section>
          <tooltip-pro
            v-if="miniState"
            :offset="[10, 8]"
            self="center left"
            anchor="center right"
          >
            Add a Request
          </tooltip-pro>
        </q-item>

        <q-item
          clickable
          v-for="item in essentialLinks"
          :key="item"
          v-ripple:secondary
          class="menu-item q-mx-sm"
          :class="item.class"
          :to="item.link"
          :href="item.href"
          @click="
            (e) => {
              if (item.action) {
                item.action();
                e.preventDefault();
                e.stopImmediatePropagation();
              }
            }
          "
        >
          <q-item-section v-if="item.icon" avatar style="min-width: unset">
            <q-icon size="sm" :name="item.icon" />
          </q-item-section>

          <q-item-section v-if="!miniState" class="no-pointer-events">
            <q-item-label>{{ item.title }}</q-item-label>
          </q-item-section>

          <tooltip-pro
            v-if="miniState"
            :offset="[10, 8]"
            self="center left"
            anchor="center right"
          >
            {{ item.title }}
          </tooltip-pro>
        </q-item>
        <q-item
          class="no-pointer-events"
          :style="{
            ['padding-top']: `calc(100vh - ${settingsTopSpace}px)`,
          }"
        >
        </q-item>

        <q-item
          clickable
          class="menu-item settings q-mb-md q-mx-sm"
          :to="$route.params?.settings_tab ? null : '/settings'"
          @click="
            (e) => {
              if (!$q.screen.gt.sm || $route.params?.settings_tab) {
                $root.toggleSettingsDrawer();
              }
            }
          "
        >
          <q-item-section
            avatar
            class="q-pr-sm"
            style="min-width: unset"
            :style="{
              ['margin-left']: !miniState ? '-10px' : 'unset',
            }"
          >
            <q-avatar rounded size="44px">
              <q-icon size="sm" name="icon-settings-02" />
            </q-avatar>
          </q-item-section>
          <q-item-section v-if="!miniState" class="no-pointer-events"
            >Settings</q-item-section
          >
          <tooltip-pro
            v-if="miniState && !$route.params?.settings_tab"
            :offset="[10, 8]"
            self="center left"
            anchor="center right"
          >
            Settings
          </tooltip-pro>
        </q-item>
      </q-list>
    </q-scroll-area>
  </q-drawer>
</template>

<script>
import { defineComponent, ref } from 'vue';
import GrowmodoTextIcon from 'src/components/GrowmodoTextIcon.vue';
import { openURL } from 'quasar';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'DrawerMain',

  components: { GrowmodoTextIcon },

  setup() {
    return {
      openURL: openURL,
      mainDrawerBreakpoint: 600,
      miniState: ref(true),
    };
  },

  computed: {
    ...mapState(useGrowmodo, ['externalLinks']),

    settingsTopSpace() {
      return 68 * this.essentialLinks.length + 250;
    },

    essentialLinks() {
      return [
        {
          title: 'Home',
          icon: 'icon-home-line',
          link: '/',
          class: 'home',
        },
        {
          title: 'Requests',
          icon: 'icon-layers-three-01',
          link: '/requests',
        },
        {
          title: 'Brands',
          icon: 'icon-brand-guidelines',
          link: '/brands',
        },
        {
          title: 'Files',
          icon: 'icon-folder',
          link: '/files',
        },
        {
          title: 'Help Center',
          icon: 'icon-message-question-square',
          link: '/help',
          action: () => openURL(this.externalLinks?.help_link),
        },
        // {
        //   title: 'Discover',
        //   icon: 'icon-compass-03',
        //   link: '/discover',
        // },
        {
          title: 'Get a Gift',
          icon: 'icon-gift-01',
          link: '/gift',
        },
      ];
    },
  },

  watch: {
    ['$root.$q.screen.width']: {
      async handler(val) {
        if (val <= this.mainDrawerBreakpoint && this.$root.mainDrawer)
          this.$root.toggleMainDrawer(false);
        else if (val > this.mainDrawerBreakpoint && !this.$root.mainDrawer)
          this.$root.toggleMainDrawer(true);
      },
    },
    ['$route.path']: {
      async handler(val) {
        if (!this.checkToExpandPath(val)) this.miniState = true;
      },
    },
  },

  methods: {
    checkToExpandPath(path) {
      if (!path) path = this.$route.path;
      return !path.startsWith('/settings');
    },

    updateMiniState(val) {
      if (this.checkToExpandPath())
        this.miniState = typeof val === 'boolean' ? val : false;
    },
  },
});
</script>
